.card {
  display: flex;
  flex-direction: column;
  width: $card-width;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__img {
    align-self: center;
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
  }

  &__item-description {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $main-color;
  }

  &__item-code {
    margin-bottom: 16px;
    font-size: 10px;
    line-height: 14px;
    color: $secondary-color;
  }

  &__feedback {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;

    &-count {
      align-self: flex-end;
      font-size: 10px;
      line-height: 14px;
      color: $main-color;
    }
  }

  &__price {
    display: flex;
    margin-bottom: 16px;
    justify-content: space-between;

    &-cost {
      font-size: 12px;
      line-height: 18px;
      color: $secondary-color;
    }

    &-value {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: $main-color;
    }
  }

  &__buy-submit {
    width: 100%;
    border: 1px solid transparent;
    font-size: 14px;
    font-weight: 700;
    text-decoration: none;
    text-align: center;
    line-height: 38px;
    color: #fff;
    background-color: $blue-color;
    border-radius: 5px;

    &:hover {
      color: $blue-color;
      background-color: #fff;
      border-color: $blue-color;
      border-radius: 5px;
    }
  }
}
